.banner {
  background-image: url(../img/Banner_img.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  margin-top: 32px;
  padding: 32px 16px;
  @media screen and (min-width: 1024px) {
    padding: 64px 0;
    margin-top: 92px;
  }
  @media screen and (min-width: 1440px) {
    padding: 128px 0;
  }
  &__title {
    display: block;
    text-align: center;
    font-size: 1.5em; // 24px
    text-transform: uppercase;
    font-weight: bold;
    @media screen and (min-width: 1024px) {
      font-size: 2em; // 32px
    }
    @media screen and (min-width: 1440px) {
      font-size: 2.5em; // 40px
    }
  }
  &__caption {
    display: block;
    text-align: center;
    font-size: 1em; // 16px
    color: #d8d8d8;
    margin-top: 16px;
    line-height: 1.5em; // 24px
  }
  &__btn {
    width: 100%;
    margin-top: 32px;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
    text-align: center;
    text-transform: uppercase;
    font-size: 0.9375em; // 15px
    line-height: 2em; // 32px
    padding: 19px 16px 16px;
    background-color: #18cfab;
    border-radius: 6px;
    box-shadow: 0px 5px 0px 0px rgb(255, 255, 255) inset;
    @media screen and (min-width: 768px) {
      width: unset;
      margin: 32px 12px 0;
    }
    @media screen and (min-width: 1024px) {
      margin: 62px 12px 0;
    }
  }
  &__btn:hover {
    background-color: #129177;
  }
}
