.services {
  display: flex;
  justify-content: center;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: 32px;
  @media screen and (min-width: 1024px) {
    margin-top: 70px;
  }
  @media screen and (min-width: 1440px) {
    padding: 0 160px;
  }
  @media screen and (min-width: 1920px) {
    padding: 0 380px;
  }
  @media screen and (min-width: 2560px) {
    padding: 0 640px;
  }
  &__content {
    margin-top: 50px;
    @media screen and (min-width: 1024px) {
      margin-top: 70px;
    }
  }
  &__link {
    width: 100%;
    display: grid;
    grid-template-columns: repeat(2, 50%);
    @media screen and (min-width: 768px) {
      grid-template-columns: repeat(3, 33%);
    }
    @media screen and (min-width: 1024px) {
      grid-template-columns: repeat(6, 1fr);
      padding: 0 16px;
    }
  }
  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    width: 100%;
    height: 72px;
    padding: 0 16px;
    background-color: #f8fcfe;
    text-decoration: none;
    border: 1px solid #edefef;
    color: #717171;
    font-size: 1em; // 16px
    position: relative;
    text-transform: capitalize;
  }
  &__item:focus {
    background-color: #18cfab;
    color: #fff;
  }
  &__item:hover {
    border: 1.5px solid #18cfab;
  }
  &__item:focus:before {
    display: none;
    content: "";
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    bottom: -20px;
    border: 10px solid transparent;
    border-top: 10px solid #18cfab;
    @media screen and (min-width: 1024px) {
      display: block;
    }
  }
  &__text-block {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 32px;
    padding: 0 16px;
    @media screen and (min-width: 768px) {
      justify-content: unset;
      flex-direction: unset;
      align-items: unset;
      display: block;
    }
    @media screen and (min-width: 1024px) {
      display: flex;
      justify-content: space-between;
      margin-top: 24px;
    }
  }
  &__text-block_img {
    display: block;
    @media screen and (min-width: 768px) {
      float: left;
      padding: 0 16px 4px 0;
    }
    @media screen and (min-width: 1024px) {
      float: unset;
      padding: 0 16px 0 0;
    }
  }
  &__text-block_article {
    margin-top: 24px;
    font-size: 0.875em; // 14px
    color: #777777;
    line-height: 1.5em; // 24px
    @media screen and (min-width: 1024px) {
      margin-top: 0;
    }
  }
}
