.advantages {
  margin-top: 8px;
  display: grid;
  grid-template-columns: repeat(2, 50%);
  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(4, 25%);
  }
  @media screen and (min-width: 1920px) {
    max-width: 1920px;
    margin: 0 auto;
  }
  &__item {
    display: flex;
    width: 100%;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding: 20px 16px;
    @media screen and (min-width: 1024px) {
      padding: 40px 16px;
    }
    @media screen and (min-width: 1440px) {
      padding: 85px 16px 103px;
    }
  }
  &__img {
    width: 50px;
    @media screen and (min-width: 1024px) {
      width: 100px;
    }
  }
  &__text {
    margin-top: 24px;
    color: #fff;
    font-weight: bold;
    text-transform: uppercase;
    font-size: 1em; // 16px
    @media screen and (min-width: 1024px) {
      font-size: 1.25em; // 20px
    }
  }
}

.advantages__item_dark-bg {
  background-color: #1c2a33;
}

.advantages__item_ligth-bg {
  background-color: #18cfab;
}

.text_color-green {
  color: #18cfab;
}
