.work {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 32px 16px;
  background-color: #e9edef;
  &__title {
    @media screen and (min-width: 1024px) {
      margin-top: 104px;
    }
  }
  &__btn {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    background-color: #18cfab;
    color: #fff;
    font-size: 0.875em; // 14px
    font-weight: bold;
    text-transform: uppercase;
    padding: 16px 22px;
    border-radius: 6px;
    margin-top: 32px;
    @media screen and (min-width: 1024px) {
      margin-top: 70px;
    }
  }
  &__btn_icon {
    margin-right: 8px;
  }
  &__btn:hover {
    background-color: #10856d;
  }
}

.block__grid {
  margin-top: 54px;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    "all all . ."
    "graphic web . ."
    "landing wordpress . .";
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-template-rows: unset;
    grid-template-areas:
      "all graphic web landing wordpress"
      ". . . . ."
      ". . . . .";
  }
  @media screen and (min-width: 1024px) {
    width: 70%;
  }
  @media screen and (min-width: 1440px) {
    width: 50%;
  }
  @media screen and (min-width: 1920px) {
    width: 35%;
  }
}
.block__grid_all {
  grid-area: all;
}
.block__grid_graphic {
  grid-area: graphic;
}
.block__grid_web {
  grid-area: web;
}
.block__grid_landing {
  grid-area: landing;
}
.block__grid_wordpress {
  grid-area: wordpress;
}

.block__grid_link {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
  height: 56px;
  padding: 0 16px;
  text-decoration: none;
  border: 1px solid #dadada;
  color: #717171;
  font-size: 0.875em; // 14px
  position: relative;
  text-transform: capitalize;
  @media screen and (min-width: 1024px) {
    margin-top: 64px;
  }
}

.block__grid_link:hover {
  color: #18cfab;
  border: 5px solid #18cfab;
  padding: 0 11px;
}

// блок с карточками

.work__content {
  width: 100%;
  margin-top: 32px;
  display: grid;
  grid-template-columns: repeat(1, 100%);
  @media screen and (min-width: 768px) {
    grid-template-columns: repeat(3, 33%);
  }
  @media screen and (min-width: 1024px) {
    margin-top: 70px;
    grid-template-columns: repeat(4, 25%);
  }
  @media screen and (min-width: 1440px) {
    padding: 0 160px;
    grid-template-columns: repeat(4, 25%);
  }
  @media screen and (min-width: 1920px) {
    padding: 0 380px;
    grid-template-columns: repeat(4, 25%);
  }
  @media screen and (min-width: 2560px) {
    grid-template-columns: repeat(6, 1fr);
  }
}

.work__content_item {
  display: block;
  width: 100%;
  position: relative;
  overflow: hidden;
  height: 206px;
  @media screen and (min-width: 768px) {
    height: 176px;
  }
  @media screen and (min-width: 1024px) {
    height: 176px;
  }
  @media screen and (min-width: 1440px) {
    height: 198px;
  }
}

.work__content_img {
  width: 100%;
}

.work__content_item_hiden {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  align-items: center;
  position: absolute;
  width: 100%;
  top: 0;
  background-color: #e9edef;
  padding: 48px 16px;
  border-top: 6px solid #18cfab;
  visibility: hidden;
  @media screen and (min-width: 768px) {
    padding: 30px 16px;
  }
  @media screen and (min-width: 1440px) {
    padding: 42px 16px;
  }
}

.work__content_item_link {
  display: block;
  font-size: 0;
  margin: 0 4px 24px;
}

.work__content_item_text,
.work__content_item_caption {
  display: block;
  width: 100%;
  text-align: center;
}

.work__content_item_text {
  font-size: 0.875em; // 14px
  font-weight: bold;
  text-transform: uppercase;
}

.work__content_item_caption {
  margin-top: 14px;
  color: #8a8a8a;
  font-size: 0.75em; // 12px
  font-weight: 300;
}
.work__content_item:hover .work__content_item_hiden {
  visibility: visible;
}

.work__content_item:focus .work__content_item_hiden {
  visibility: visible;
}
