.header {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  background-image: url(../img/bg_main.jpg);
  background-repeat: no-repeat;
  background-position: top center;
  &__block {
    background: rgba(94, 93, 88, 0.6);
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 16px;
    @media screen and (min-width: 1024px) {
      padding: 0 24px;
    }
    @media screen and (min-width: 1440px) {
      padding: 0 240px;
    }
    @media screen and (min-width: 1920px) {
      padding: 0 380px;
      max-width: 1920px;
    }
    @media screen and (min-width: 2560px) {
      justify-content: space-around;
    }
  }
  &__block_logo-icon {
    display: flex;
    align-items: center;
    text-decoration: none;
  }
  &__block_logo-text {
    display: none;
    padding-left: 6px;
    color: #fff;
    font-weight: normal;
    font-size: 2em; //32px
    @media screen and (min-width: 768px) {
      display: block;
    }
  }
  &__block_logo-color {
    color: #18cfab;
    font-weight: bold;
  }
}

.title {
  width: 100%;
  padding: 0 16px 72px;
  text-align: center;
  justify-content: center;
  margin-top: 72px;
  display: flex;
  flex-wrap: wrap;
  @media screen and (min-width: 1024px) {
    margin-top: 264px;
    padding-bottom: 242px;
  }
  &__text {
    width: 100%;
    font-weight: bold;
    font-size: 2em; // 32px;
    color: #fff;
    margin: 0;
    text-transform: uppercase;
    margin-top: 22px;
    @media screen and (min-width: 1024px) {
      font-size: 4.875em; //78px
    }
  }
  &__before {
    width: 100%;
    color: #fff;
    text-transform: uppercase;
    font-weight: bold;
    @media screen and (min-width: 1024px) {
      font-size: 2.1875em; //35px
    }
  }
  &__after {
    width: 100%;
    color: #d4d4d4;
    text-transform: uppercase;
    font-weight: normal;
    margin-top: 22px;
    @media screen and (min-width: 1024px) {
      font-size: 2.125em; //34px
    }
  }
  &__btn {
    width: 100%;
    margin-top: 32px;
    font-weight: bold;
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    font-size: 0.9375em; // 15px
    line-height: 2em; // 32px
    padding: 19px 16px 16px;
    background-color: #18cfab;
    border-radius: 6px;
    box-shadow: 0px 5px 0px 0px rgb(255, 255, 255) inset;
    @media screen and (min-width: 768px) {
      width: 30%;
      margin: 32px 12px 0;
    }
    @media screen and (min-width: 1024px) {
      width: unset;
      margin: 122px 12px 0;
    }
  }
  &__btn:hover {
    background-color: #129177;
  }
  &__btn:last-child {
    margin-top: 12px;
    background-color: #00cbf6;
    @media screen and (min-width: 768px) {
      margin-top: 32px;
    }
    @media screen and (min-width: 1024px) {
      margin-top: 122px;
    }
  }
  &__btn:last-child:hover {
    background-color: #00819b;
  }
}
