.container {
  font-family: "Montserrat", sans-serif;
  font-size: 16px;
}

.text_color-green {
  color: #18cfab;
}

// Стиль для h2
.block-title {
  font-size: 1.5em; // 24px
  font-weight: normal;
  color: #4e4e4e;
  position: relative;
  text-align: center;
  @media screen and (min-width: 1024px) {
    font-size: 2.25em; // 36px
  }
}

.block-title:before {
  content: "";
  display: block;
  width: 76px;
  height: 1px;
  background-color: #18cfab;
  position: absolute;
  bottom: -24px;
  left: 50%;
  transform: translateX(-45%);
  @media screen and (min-width: 768px) {
    left: 50%;
    transform: translateX(-50%);
  }
  @media screen and (min-width: 1024px) {
    transform: translateX(-50%);
  }
}

.block-title::after {
  content: "";
  display: block;
  width: 76px;
  height: 1px;
  background-color: #18cfab;
  position: absolute;
  bottom: -28px;
  left: 55%;
  transform: translateX(-55%);
  @media screen and (min-width: 768px) {
    transform: translateX(-80%);
  }
  @media screen and (min-width: 1024px) {
    transform: translateX(-100%);
  }
  @media screen and (min-width: 1440px) {
    left: 50%;
    transform: translateX(-40%);
  }
}
