// Выпадающее меню //
.nav {
  &-toggle {
    display: none;
  }
}

.nav__btn {
  display: flex;
  align-items: center;
  right: 20px;
  width: 20px;
  cursor: pointer;
  z-index: 2;
  position: absolute;
}

/* гамбургер */
.nav__btn > span,
.nav__btn > span::before,
.nav__btn > span::after {
  display: block;
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #e5e5e5;
  @media screen and (min-width: 1024px) {
    display: none;
  }
}

.nav__btn > span::before {
  content: "";
  top: -8px;
}
.nav__btn > span::after {
  content: "";
  top: 8px;
}

/* контейнер меню */
.nav__box {
  display: block;
  position: fixed;
  top: 0;
  right: -100%;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 80px 0;
  list-style: none;
  text-align: center;
  background-color: #6b6b6b;
  z-index: 1;
  @media screen and (min-width: 1024px) {
    background-color: unset;
    position: unset;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 0;
  }
}
/* элементы меню */
.nav__link {
  display: block;
  margin: 26px 12px;
  color: rgba(255, 255, 255, 0.6);
  font-family: "Montserrat", sans-serif;
  font-size: 1.125em;
  text-transform: uppercase;
  font-weight: bold;
  text-decoration: none;
  @media screen and (min-width: 1024px) {
    margin: 46px 12px;
  }
}
.nav__link:hover {
  margin: 26px 12px;
  color: #18cfab;
  @media screen and (min-width: 1024px) {
    margin: 0;
    padding: 46px 12px 44px;
    border-bottom: 2px solid #18cfab;
  }
}

.nav__link:focus {
  margin: 26px 12px;
  color: #0ff0c3;
  @media screen and (min-width: 1024px) {
    margin: 0;
    padding: 46px 12px 44px;
    border-bottom: 2px solid #0ff0c3;
  }
}

#nav__toggle:checked ~ .nav__btn > span {
  transform: rotate(45deg);
}
#nav__toggle:checked ~ .nav__btn > span::before {
  top: 0;
  transform: rotate(0);
}
#nav__toggle:checked ~ .nav__btn > span::after {
  top: 0;
  transform: rotate(90deg);
}

#nav__toggle:checked ~ .nav__box {
  right: 0;
}

.nav__btn > span,
.nav__btn > span::before,
.nav__btn > span::after {
  transition-duration: 0.25s;
}
.nav__box {
  transition-duration: 0.25s;
}
.nav__item {
  transition-duration: 0.25s;
}

.search {
  position: absolute;
  top: 45px;
  left: 20px;
  @media screen and (min-width: 1024px) {
    position: unset;
    top: unset;
    left: unset;
  }
  &__field {
    display: none;
  }
  &__btn {
    background: none;
    border: none;
  }
}

.fa-search {
  color: #e5e5e5;
  font-size: 1.25em; // 20px
  @media screen and (min-width: 1024px) {
    margin-left: 10px;
    color: rgba(255, 255, 255, 0.6);
  }
}
