.footer {
  display: flex;
  justify-content: center;
  padding: 24px 0;
  width: 100%;
  background-color: #22353f;
  @media screen and (min-width: 1024px) {
    padding: 48px 0;
  }
  @media screen and (min-width: 1920px) {
    max-width: 1920px;
    margin: 0 auto;
  }
  &__text {
    color: #aeaeae;
    font-size: 0.875em; // 14px
    font-weight: 300;
  }
}
